.backdrop {
	padding: 0px 15px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.cartDialogContainer {
	display: flex;
	flex-direction: column;
	background-color: #ffffff;
	gap: 67px;
	border-radius: 8px;
	max-width: 735px;
	width: 100%;
	height: 424px;
}

@media (max-width: 521px) {
	.cartDialogContainer {
		gap: 20px;
		height: 288px;
	}
}

.dialogCross {
	display: flex;
	justify-content: flex-end;
	padding: 36px;
}

@media (max-width: 521px) {
	.dialogCross {
		padding: 16px;
	}
}

.dialogButton {
	margin-bottom: 14px;
}

.dialogInfo {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 10px;
}

.dialogTitle {
	font-weight: 500;
	font-size: 40px;
	line-height: 48px;
	text-transform: uppercase;
}

@media (max-width: 768px) {
	.dialogTitle {
		font-size: 30px;
		line-height: 38px;
	}
}
@media (max-width: 487px) {
	.dialogTitle {
		font-size: 20px;
		line-height: 24px;
	}
}

.dialogText {
	font-weight: 300;
	font-size: 20px;
	line-height: 24px;
	color: #3f4e65;
	text-align: center;
}

@media (max-width: 768px) {
	.dialogText {
		font-size: 14px;
		line-height: 18px;
	}
}

@media (max-width: 487px) {
	.dialogText {
		font-size: 12px;
		line-height: 14px;
	}
}
