.catalogList {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(290px, 326px));
	justify-content: center;
	width: 100%;
	gap: 10px 20px;
	padding: 0;
}

.productWrapper {
	display: flex;
	flex-direction: column;
	gap: 50px;
	justify-content: space-between;
	width: 100%;
	padding-bottom: 60px;
}

.productCardsContainer {
	display: flex;
	width: 100%;
	box-shadow: 0px 15px 70px -11px rgba(43, 28, 1, 0.04);
}
